body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: black;
}

.main-container {
  display: -webkit-flex;
  display: flex;
  margin: 50px;
}

.sub-container {
  display: -webkit-flex;
  display: flex;
}

.menu-container {
  min-width: 40vmin;
}

.title-menu {
  font-size: 5vmin;
}

.title-menu ~ div {
  font-size: 4vmin;
}

.title-menu ~ div:hover, .title-menu ~ div > a:hover {
  font-size: 5vmin;
  color: #003366;
}

.detail-container {
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0px 50px;
}

.project-container > div {
  margin: 0px 0px 50px 0px;
}

.photo {
  height: 40vmin;
  border-radius: 20px;
}
